h2 {
    font-size: 1.2em;
    margin-top: 0px
}

p {
    font-family: "Trebuchet MS";
    font-size: 1em;
    align-content: left;
}

i {
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
}

.down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}

.up {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
}